nav a {
	text-decoration: none;
}

nav ul {
	margin: 0;
	padding: 0;
}

nav li {
	list-style-position: inside;
	font: 14px 'Oswald', sans-serif;
	padding: 10px;
}

.top-menu li {
	display: inline-block;
	padding: 10px 30px;
	margin: 0;
}


.top-menu li.active {
	background: #29c5e6;
    color: #fff;
}

.top-menu a {
	color: #b2b2b2;
}


.top-menu a:hover {
	color: #ffffff;
}

.logo{
	float: left;
	color: #b2b2b2;
	list-style: none;

}

.logout{
	float: right;
}






